import React from "react";
import Layout from "./Layout";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center"
  },
  contents: {
    marginTop: "20px",
    fontSize: "5em"
  }
};
const View = () => {
  return (
    <Layout title="View">
      <div style={styleSheet.container}>
        <div style={styleSheet.contents}>View</div>
      </div>
    </Layout>
  );
};

export default View;
